
html,body{background-color:#fcfcfc !important;color:#586E75 !important
}
html #app,body #app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center
}
html #app .header,body #app .header{background-color:#073642;color:#fcfcfc;text-align:left
}
html #app .header .title,body #app .header .title{padding:10px 0 0 40px;font-size:32px;font-weight:bold
}
html #app .header .subtitle,body #app .header .subtitle{padding:2px 0 2px 75px;background-color:#002B36;font-size:16px
}
html #app .main>.container-fluid>.row>.som-column,body #app .main>.container-fluid>.row>.som-column{padding-top:10px
}
html #app .main>.container-fluid>.row>.recipe-list-column,body #app .main>.container-fluid>.row>.recipe-list-column{padding-top:10px;height:calc(100vh - 85px);min-height:calc(100vh - 85px);max-height:calc(100vh - 85px);overflow:auto
}



